@use "global" as *;

.top-price {
		background-color: $greenpale;
		z-index:-100;
		padding-top: rem(80);
		padding-bottom: rem(82);
			@include mq(md) {
			padding-top: rem(100);
			padding-bottom: rem(102);
	}
}
.top-price__inner {
	position: relative;
	z-index:1;
	margin-inline:auto;
	width:100%;
	max-width: rem(1077);
}

.top-price__main {
		margin-top: rem(28);
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include mq(md) {
				margin-top: rem(40);
        flex-direction: row-reverse;
        flex-wrap: nowrap;
				justify-content: space-between;
				// align-items: flex-start;
				gap:calc(calc(60 / 1077) * 100%);
    }
}

.top-price__lists {
    @include mq(md) {
    flex: 1;
		max-width: rem(492);
		padding-left: rem(30);
    }
}

.top-price__image {
	width:100%;

	@include mq(md) {
			max-width: rem(492);
	}
}
.top-price__image img {
	width: 100%;
  height: auto;
  object-fit: cover;
	aspect-ratio: 345 / 227;
	@include mq(md) {
		aspect-ratio: 492 / 746;
	}
}

/* 画像が右から左へふわっと出現するアニメーション */
@keyframes fadeInRight {
    from {
        opacity: 0; /* 最初は見えない */
        transform: translateX(50px); /* 右側から50pxずらした位置 */
    }
    to {
        opacity: 1; /* 完全に表示 */
        transform: translateX(0); /* 元の位置 */
    }
}

/* アニメーションの基本スタイル */
/* 初期状態では見えない */
.fade-in-right {
    opacity: 0;
    transform: translateX(70px);
    transition: opacity 1.5s ease-out, transform 1.5s ease-out;
}

/* アニメーションを適用するクラス（JavaScriptで追加） */
.fade-in-right.is-visible {
    opacity: 1;
    transform: translateX(0);
}




.top-price__button {
	z-index: 1000;
	margin-top: rem(37);
	text-align: center;
	padding-bottom: rem(4);

	@include mq(md) {
			margin-top: rem(80);
	}
}
